import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Lineup from '@/components/Lineup'
import FormInput from 'bootstrap-vue/components/form-input.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Lineup',
      component: Lineup
    }
  ]
})



